/* / */
blockquote {
  position: relative;
  color: #999;
  font-weight: 400;
  border-left: 6px solid #1abc9c;
  padding-left: 1em;
  margin: 1em 3em 1em 2em;
}

@media #{$small-only} {
  blockquote {
    margin: 1em 0;
  }
}

address, caption, cite, code, dfn, em, th, var {
  font-style: normal;
  font-weight: 400;
}

.code-inline{
  margin:0 4px;
  font-size: 13px;
  background-color: #f8f8f8;
  background-color: rgba(#f8f8f8,0.65);
  padding: 2px 4px;
  color: #c7254e;
  white-space: nowrap;
  border-radius: 3px;
  border:1px solid rgba(#c7254e,0.15);
}

q:before, q:after {
  content: '';
}



/*  u  html5 Draft
 *  class="typo-u" 
 *  <u> http://www.whatwg.org/specs/web-apps/current-work/multipage/text-level-semantics.html#the-u-element
 *  4 http://www.w3.org/TR/html401/appendix/changes.html#idx-deprecated
 *  <u> http://html5doctor.com/u-element/
 */
u, .typo-u {
  text-decoration: underline;
}

/*  */
mark {
  background: #fffdd1;
  border-bottom: 1px solid #ffedce;
  padding: 2px;
  margin: 0 5px;
}

/*  horizontal rule */
hr {
  border: none;
  border-bottom: 1px solid #cfcfcf;
  margin-bottom: 0.8em;
  height: 10px;
}

/*  */
small, .typo-small,
  /*  */
figcaption {
  font-size: 0.9em;
  color: #888;
}

/* / */
.typo p, .typo pre, .typo ul, .typo ol, .typo dl, .typo form, .typo hr, .typo table,
.typo-p, .typo-pre, .typo-ul, .typo-ol, .typo-dl, .typo-form, .typo-hr, .typo-table, blockquote {
  margin-bottom: 1.2em
}

.typo-p--indent{
  text-indent: 2em;
}
.typo-p--sag:first-letter{
  font-size:175%;
}

/* margin  */
.typo h1, .typo h2, .typo h3, .typo h4, .typo h5, .typo h6,
.typo-h1, .typo-h2, .typo-h3, .typo-h4, .typo-h5, .typo-h6 {
  margin-top: 1.2em;
  margin-bottom: 0.6em;
  line-height: 1.35;

  small{
    font-size: 75%;
    font-weight: 500;
    color:$grayDark;
  }
}

/*  core/common  */

.typo h1, .typo-h1 {
  font-size: 2em;
}

.typo h2, .typo-h2 {
  font-size: 1.8em;
}

.typo h3, .typo-h3 {
  font-size: 1.6em;
}

.typo h4, .typo-h4 {
  font-size: 1.4em;
}

.typo h5, .typo h6, .typo-h5, .typo-h6 {
  font-size: 1.2em;
}

.typo li ul, .typo-ul ul, .typo-ol ul {
  list-style: circle;
}

/*  ul/ol table  */
.typo table th, .typo table td, .typo-table th, .typo-table td, .typo table caption {
  border: 1px solid #ddd;
  padding: 0.5em 1em;
  color: #666;
}


/* 100 */
.typo-em {
  position: relative;
}

.typo-em:after {
  position: absolute;
  top: 0.65em;
  left: 0;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  letter-spacing: 0.6em;
  text-indent: 0.4em;
  content: "\2022\2022\2022\2022\2022\2022\2022\2022\2022\2022\2022\2022\2022\2022\2022\2022\2022\2022";
}

/* Responsive images */
.typo img {
  max-width: 100%;
}