@charset "UTF-8";
//-----------------------------------------------------
// setting scss
//-----------------------------------------------------


// 
//-----------------------------------------------------

$lte7:                  false  !default; //ie6-7
$filter:                true  !default; //ie8- css3
$openResponse:          true  !default; //
$printStyleSwitch:      false !default; //


//  px || rem
//-----------------------------------------------------

$px:                    'px' !default;

// 
//-----------------------------------------------------
$wrapWidth:             990px !default;   // 
$wrapWideWidth:         1190px !default;  // 
$wrapMobileWidth:       100% !default;    // 
$gridNum:               12 !default;      // 


// 
//-----------------------------------------------------
/* 

  

      .at-mg-small{
            margin:10px;
      }

*/

$spaceBase  :  (small:0.5rem,mid:1rem,large:2rem,exlarge:3rem);

// font
//-----------------------------------------------------
$rootFontSize:          10px;           // 
$baseFontSize:          $rootFontSize * 1.2 !default; // 
$baseLineHeight:        1.25 !default;   // 
$baseFontFamily:        "Segoe UI", arial, "Hiragino Sans GB", "\5b8b\4f53", sans-serif !default;


$fontSerif:             "Segoe UI","Helvetica Neue",arial,"Source Han sans","Hiragino Sans GB","\5b8b\4f53";
$fontSans:              "Segoe UI","Helvetica Neue",arial,"Source Han sans","Hiragino Sans GB","\5FAE\8F6F\96C5\9ED1","\9ED1\4F53","\534E\6587\9ED1\4F53";

$fontSun:               "\5b8b\4f53"; //
$fontHei:               "Segoe UI","Helvetica Neue",arial,"Hiragino Sans GB","\5FAE\8F6F\96C5\9ED1","\9ED1\4F53","\534E\6587\9ED1\4F53"; //   
$fontMonospace:          Monaco,Menlo,Consolas,"Courier New",monospace;
$fontKaiti:              Georgia,"Times New Roman","stkaiti","\6977\4F53","kaiti gb2312";

// font level size
$xsFontSize:            $rootFontSize * 1.2;
$smFontSize:            $rootFontSize * 1.4;
$mediumFontSize:        $rootFontSize * 1.6;
$lgFontSize:            $rootFontSize * 1.8;
$xlFontSize:            $rootFontSize * 2.4;
$xxlFontSize:           $rootFontSize * 2.8;
$xxxlFontSize:          $rootFontSize * 3.2;

//-----------------------------------------------------
//  
//  font_icons/variables


$resUrl:                "/";
$imgUrl:                "../images/";
// $fontUrl:               "../fonts/";



// 
//-----------------------------------------------------
$baseGap:               20px !default;


// 
/*
** color function : http://sass-lang.com/docs/yardoc/Sass/Script/Functions.html
** lighten($color, $amount)
** darken($color, $amount)
** opacify($color, $amount)
*/

//-----------------------------------------------------
$black:                 #000 !default;
$blackLight:            #333 !default;
$grayDarker:            #666 !default;
$grayDark:              #999 !default;
$gray:                  #ccc !default;
$grayLight:             #f5f5f5 !default;
$grayLightx:            #fafafa !default;
$white:                 #fff !default;

$red:                   #cc3300 !default;
$blue:                  #00a8e6 !default;
$blueDark:              #47acca !default;
$orange:                #f96340 !default;
$green:                 #6ea104 !default;
$yellow:                #ffe400 !default;
$pink:                  #c3325f !default;
$purple:                #7a43b6 !default;


// link
//-----------------------------------------------------
// darken 5%
// 
$linkColor:             rgb(0,119,211) !default;


// scaffolding
//-----------------------------------------------------
$bodyBgColor:           #fff !default;
$textColor:             #333 !default;


// placeholder
//-----------------------------------------------------
// placeholder
$placeholder:           true $gray !default;


// 
// 
//-----------------------------------------------------
// normalhoveractive
// darken  lighten 

$defaultColor:                 #666666 !default;
$primaryColor:                 #d7342e !default;
$secondaryColor:               #2e6da4 !default;
$infoColor:                    #47A4C0 !default;
$noticeColor:                  #2185d0 !default;
$QuestionColor:                #e03997 !default;
$warningColor:                 #c57b19 !default;
$errorColor:                   #dc1e18 !default;
$successColor:                 #287128 !default;
$untreatedColor:               #a5673f !default;
$disabledColor:                #f5f5f5 !default;

$defaultBorderColor:           lighten($defaultColor,32%) !default;
$primaryBorderColor:           lighten($primaryColor,8%) !default;
$secondaryBorderColor:         lighten($secondaryColor,18%) !default;
$infoBorderColor:              lighten($infoColor,6%) !default;
$noticeBorderColor:            lighten($noticeColor,8%) !default;
$QuestionBorderColor:          lighten($QuestionColor,8%) !default;
$warningBorderColor:           lighten($warningColor,8%) !default;
$errorBorderColor:             lighten($errorColor,8%) !default;
$successBorderColor:           lighten($successColor,8%) !default;
$untreatedBorderColor:         #999 !default;
$disabledBorderColor:          #ccc !default;

$defaultBgColor:               lighten($defaultColor,42%) !default;
$primaryBgColor:               lighten($primaryColor,36%) !default;
$secondaryBgColor:             lighten($secondaryColor,60%) !default;
$infoBgColor:                  lighten($infoColor,40%) !default;
$noticeBgColor:                lighten($noticeColor,60%) !default;
$QuestionBgColor:              lighten($QuestionColor,60%) !default;
$warningBgColor:               lighten($warningColor,48%) !default;
$errorBgColor:                 lighten($errorColor,60%) !default;
$successBgColor:               lighten($successColor,60%) !default;
$untreatedBgColor:             #999 !default;
$disabledBgColor:              #ccc !default;


// Map

//-----------------------------------------------------
// 

$colorMap:(
      default:    $defaultColor,
      primary:    $primaryColor,
      secondary:  $secondaryColor,
      info:       $infoColor,
      notice:     $noticeColor,
      quest:   $QuestionColor,
      warn:    $warningColor,
      error:      $errorColor,
      succ:    $successColor,
      untreated:  $untreatedColor,
      disabled:   $disabledColor
);

$bgColorMap:(
      default:    $defaultBgColor,
      primary:    $primaryBgColor,
      secondary:  $secondaryBgColor,
      info:       $infoBgColor,
      notice:     $noticeBgColor,
      quest:   $QuestionBgColor,
      warn:    $warningBgColor,
      error:      $errorBgColor,
      succ:    $successBgColor,
      untreated:  $untreatedBgColor,
      disabled:   $disabledBgColor
);

// 
//-----------------------------------------------------
// 
$tipInfo:                    #edf4fa #1c75bb !default;
$tipNotice:                  #edf4fa #1c75bb !default;
$tipQuestion:                #edf4fa #1c75bb !default;
$tipSuccess:                 #ebf9df #58991f !default;
$tipWarning:                 #faeed4 #c59120 !default;
$tipError:                   #faedee #cc220b !default;

// radius
//-----------------------------------------------------
$baseRadius:            3px !default;
$smallRadius:           2px !default;


// z-index
//-----------------------------------------------------
$zindexDropdown:        1000 !default;
$zindexFixedTopbar:     1010 !default;
$zindexTooltip:         1020 !default;
$zindexAlert:           1030 !default;
$zindexModal:           1100 !default;
$zindexMask:            2000 !default;
$zindexPop:             2900 !default;

// Media Query 
//-----------------------------------------------------
$xsmall-point:  480px;
$small-range:   0 640px;       /* 0, 640px */
$medium-range:  641px 1024px;  /* 641px, 1024px */
$large-range:   1025px 1440px;  /* 1025px, 1440px */
$xlarge-range:  1441px 1920px; /* 1441px, 1920px */
// $xxlarge-range: (1921px);       /* 1921px */


$screen:       "only screen" !default;
$landscape:    "#{$screen} and (orientation: landscape)" !default;
$portrait:     "#{$screen} and (orientation: portrait)" !default;

$xsmall:       "#{$screen} and (max-width: #{$xsmall-point})" !default;
$small-up:     $screen !default;
$small-only:   "#{$screen} and (max-width: #{nth($small-range,2)})" !default;
$medium-down:  "#{$screen} and (max-width:#{nth($medium-range,2)})" !default;
$medium-up:    "#{$screen} and (min-width:#{nth($medium-range,2)})" !default;
$medium-only:  "#{$screen} and (min-width:#{nth($medium-range,1)}) and (max-width:#{nth($medium-range,2)})" !default;
$large-up:     "#{$screen} and (min-width:#{nth($large-range,1)})" !default;
$large-only:   "#{$screen} and (min-width:#{nth($large-range,1)}) and (max-width:#{nth($large-range,2)})" !default;
$xlarge-up:    "#{$screen} and (min-width:#{nth($xlarge-range,1)})" !default;
$xlarge-only:  "#{$screen} and (min-width:#{nth($xlarge-range,1)}) and (max-width:#{nth($xlarge-range,2)})" !default;

// $xxlarge-up:   "#{$screen} and (min-width:#{nth($xxlarge-range,1)})" !default;
// $xxlarge-only: "#{$screen} and (min-width:#{nth($xxlarge-range,1)}) and (max-width:#{nth($xxlarge-range,2)})" !default;
